package dev.jamesyox.svgk.attr.types

import dev.jamesyox.svgk.attr.AttributeContainer
import dev.jamesyox.svgk.attr.InMemoryAttributeContainer
import kotlin.reflect.KProperty

public class SvgAttribute<T>(
    private val name: String,
    private val typeToken: AttributeTypeToken<T>,
) {
    //@Deprecated("Write Only", level = DeprecationLevel.ERROR)
    public operator fun getValue(thisRef: AttributeContainer, property: KProperty<*>): T? {
        throw UnsupportedOperationException()
    }

    public operator fun setValue(thisRef: AttributeContainer, property: KProperty<*>, value: T?) {
        value?.let { thisRef[name] = typeToken.intoString(it) }
    }
}

public fun <T>svgAttribute(name: String, token: AttributeTypeToken<T>): SvgAttribute<T> {
    return SvgAttribute(name, token)
}

public fun noGet(): Nothing {
    throw UnsupportedOperationException()
}