package dev.jamesyox.svgk.tags

import dev.jamesyox.svgk.SvgTag
import dev.jamesyox.svgk.SvgTagDSL
import dev.jamesyox.svgk.TagConsumer
import dev.jamesyox.svgk.attr.AttributeContainer
import dev.jamesyox.svgk.consumeTag
import dev.jamesyox.svgk.tags.categories.ContainerElement
import dev.jamesyox.svgk.tags.categories.Transformable

public typealias Group = G

public data object G : SvgTag, Transformable, ContainerElement {
    override val tagName: String = "g"
}

context(tagConsumer: TagConsumer<T>, _: ContainerElement)
public fun <T> g(
    content: context (AttributeContainer, @SvgTagDSL G) () -> Unit,
): T {
    return consumeTag(
        tag = G,
        content = { context(G) { content() } }
    )
}