package dev.jamesyox.svgk.attr.objs.pathd

import dev.jamesyox.svgk.attr.types.svgString

internal typealias EllipticalArcCurve = A

internal class A(
    rx: Number,
    ry: Number,
    angle: Number,
    largeArcFlag: Boolean,
    sweepFlag: Boolean,
    x: Number,
    y: Number
) : PathCommand {
    override val svgString: String = "A $rx $ry $angle ${largeArcFlag.svgString} ${sweepFlag.svgString} $x $y"
}