package dev.jamesyox.statik

import dev.jamesyox.statik.css.Selector
import kotlinx.css.CssBuilder
import kotlinx.css.CssDsl
import kotlinx.css.StyledElement
import kotlinx.html.CommonAttributeGroupFacade
import kotlinx.html.classes
import kotlinx.html.style

// TODO: Inline classes dont support varags [KT-33565]
public fun CommonAttributeGroupFacade.classSelectors(classSelectors: List<Selector.Class>) {
    classes = classSelectors.map { it.name }.toSet()
}

public fun CommonAttributeGroupFacade.classSelector(classSelector: Selector.Class) {
    classes += classSelector.name
}

public fun CommonAttributeGroupFacade.css(block: @CssDsl CssBuilder.() -> Unit) {
    style = CssBuilder().apply(block).toString()
}

public fun CommonAttributeGroupFacade.css(styledElement: StyledElement) {
    style = styledElement.declarations.toString()
}


