package dev.jamesyox.svgk.attr.types

import dev.jamesyox.svgk.attr.AttributeContainer
import dev.jamesyox.svgk.attr.SvgObject
import kotlin.collections.joinToString

context(ac: AttributeContainer)
public fun <T : SvgObject> listAttribute(
    key: String,
    separator: String,
    value: List<T>
) {
    ac[key] = value.svgString(separator)
}

context(ac: AttributeContainer)
public fun numberListAttribute(
    key: String,
    separator: String,
    value: List<Number>
) {
    ac[key] = value.joinToString(separator = separator) { it.svgString }
}

internal fun List<SvgObject>.svgString(separator: String) = joinToString(separator = separator) { it.svgString }