package dev.jamesyox.svgk

import dev.jamesyox.svgk.attr.AttributeContainer
import dev.jamesyox.svgk.attr.InMemoryAttributeContainer
import dev.jamesyox.svgk.attr.`class`
import dev.jamesyox.svgk.tags.Svg
import kotlinx.browser.document
import org.w3c.dom.Document
import org.w3c.dom.Node
import org.w3c.dom.get
import org.w3c.dom.svg.SVGElement


public class JsDomTagConsumer(
    private val document: Document,
) : TagConsumer<SVGElement>, AttributeContainer {
    private val domStack = arrayListOf<SVGElement>()
    private var rootElements: MutableList<SVGElement> = mutableListOf()

    override val attributeContainer: InMemoryAttributeContainer = InMemoryAttributeContainer()

    public override var last: SVGElement? = null
        private set

    override fun set(key: String, value: String) { attributeContainer[key] = value }
    override fun get(key: String): String? = attributeContainer[key]

    override val attributesMap: Map<String, String> get() {
        return (0 until (last?.attributes?.length ?: 0) step 1).mapNotNull {
            last?.attributes?.item(it)?.let { attr -> Pair(attr.name, attr.value) }
        }.toMap()
    }

    override fun onTagStart(tag: SvgTag) {
        val element: SVGElement = document.createElementNS(
            namespace = "http://www.w3.org/2000/svg",
            qualifiedName = tag.tagName
        ) as SVGElement

        if (domStack.isNotEmpty()) {
            domStack.last().appendChild(element)
        } else {
            rootElements.add(element)
        }
        domStack.add(element)
        val elementAttributeContainer = SvgElementAttributeContainer(element)
        // Dump memory contents into DOM node
        attributeContainer.attributesMap.forEach {
            elementAttributeContainer[it.key] = it.value
        }
        attributeContainer.clear()
    }

    override fun onTagContent(content: String) {
        domStack.last().textContent = content
    }

    override fun onTagEnd(tag: SvgTag) {
        if (domStack.isEmpty() || domStack.last().tagName.lowercase() != tag.tagName.lowercase()) {
            throw IllegalStateException("We haven't entered tag ${tag.tagName} but trying to leave")
        }
        last = domStack.removeAt(domStack.lastIndex)
    }

    override fun output(): List<SVGElement> {
        return rootElements.toList()
    }
}

public inline fun svgElement(block: context(TagConsumer<SVGElement>) () -> Unit): List<SVGElement> {
    return JsDomTagConsumer(document).apply(block).output()
}

public fun Node.appendSvg(block: context(TagConsumer<SVGElement>) () -> Unit): List<SVGElement> {
    val consumer = JsDomTagConsumer(document).delayed().apply(block)
    return consumer.output().onEach { appendChild(it) }
}

public inline fun Node.replaceSvg(block: context(TagConsumer<SVGElement>, Svg) () -> Unit): List<SVGElement> {
    val consumer = svgElement { context(Svg) { block() } }
    for (i in 0 until childNodes.length) {
        childNodes[0]?.let { removeChild(it) }
    }
    return consumer.onEach(::appendChild)
}
