package dev.jamesyox.svgk.attr

import dev.jamesyox.svgk.attr.types.svgString
import kotlin.time.Duration

public interface SvgObject {
    public val svgString: String
}

public abstract class ConstantSvgObject(
    override val svgString: String
): SvgObject

public abstract class WrappedSvgObject(svgObject: SvgObject): SvgObject by svgObject
public abstract class WrappedNumberSvgObject(number: Number) : SvgObject {
    override val svgString: String = number.svgString
}
public abstract class WrappedDurationSvgObject(duration: Duration) : SvgObject {
    override val svgString: String = duration.svgString
}
public abstract class WrappedListSvgObject(separator: String, list: List<SvgObject>): SvgObject {
    override val svgString: String = list.svgString(separator)
}