package dev.jamesyox.svgk.tags

import dev.jamesyox.svgk.SvgTagDSL
import dev.jamesyox.svgk.TagConsumer
import dev.jamesyox.svgk.attr.AttributeContainer
import dev.jamesyox.svgk.consumeTag
import dev.jamesyox.svgk.tags.categories.ContainerElement

@SvgTagDSL
public data object Svg : ContainerElement {
    override val tagName: String = "svg"
}

context(_: TagConsumer<T>)
public fun <T> svg(
    content: context(AttributeContainer, TagConsumer<T>, @SvgTagDSL Svg) () -> Unit
): T {
    return consumeTag(
        tag = Svg,
        content = { context(Svg) { content() } }
    )
}
