package dev.jamesyox.svgk

import dev.jamesyox.svgk.attr.AttributeContainer
import org.w3c.dom.svg.SVGElement

public class SvgElementAttributeContainer(
    private val svgElement: SVGElement
) : AttributeContainer {
    override fun set(key: String, value: String) {
        svgElement.setAttribute(key, value)
    }

    override fun get(key: String): String? {
        return svgElement.getAttribute(key)
    }

    override val attributesMap: Map<String, String> get() {
        return (0 until svgElement.attributes.length step 1).mapNotNull {
            svgElement.attributes.item(it)?.let { attr -> Pair(attr.name, attr.value) }
        }.toMap()
    }
}

public fun <T> SVGElement.attrs(block: AttributeContainer.() -> T): T {
    return SvgElementAttributeContainer(this).let(block)
}