package dev.jamesyox.svgk

import dev.jamesyox.svgk.attr.AttributeContainer

public interface TagConsumer<out T> {
    public fun onTagStart(tag: SvgTag)
    public fun onTagContent(content: String)
    public fun onTagEnd(tag: SvgTag)
    public fun output(): List<T>
    public val last: T?
    public val attributeContainer: AttributeContainer
}

context(tagConsumer: TagConsumer<T>)
internal inline fun <T> consumeTerminalTag(
    tag: SvgTag,
    content: String,
    attributes: context(AttributeContainer) () -> Unit
): T {
    context(tagConsumer.attributeContainer) {
        attributes()
    }
    tagConsumer.onTagStart(tag)
    tagConsumer.onTagContent(content)
    tagConsumer.onTagEnd(tag)
    return tagConsumer.last!!
}

context(tagConsumer: TagConsumer<T>)
internal inline fun <T> consumeTag(
    tag: SvgTag,
    content: context(AttributeContainer, TagConsumer<T>) () -> Unit
): T {
    // Grab out the attributes. Ignore tags for now
    tagConsumer.onTagStart(tag)
    content(tagConsumer.attributeContainer, tagConsumer)
    tagConsumer.onTagEnd(tag)
    return tagConsumer.last!!
}