package dev.jamesyox.svgk.tags

import dev.jamesyox.svgk.SvgTagDSL
import dev.jamesyox.svgk.TagConsumer
import dev.jamesyox.svgk.attr.AttributeContainer
import dev.jamesyox.svgk.consumeTag
import dev.jamesyox.svgk.tags.categories.Shape
import dev.jamesyox.svgk.tags.categories.ShapeContainer
import dev.jamesyox.svgk.tags.categories.Transformable

public data object Circle : Shape, Transformable {
    override val tagName: String = "circle"
}

context(_: TagConsumer<T>, _: ShapeContainer)
public fun <T> circle(
    content: context(AttributeContainer, @SvgTagDSL Circle) () -> Unit,
): T {
    return consumeTag(
        tag = Circle,
        content =  { context(Circle) { content() } }
    )
}

