package dev.jamesyox.svgk.attr.types

import kotlin.time.Duration

internal data object DurationTypeToken : AttributeTypeToken<Duration> {
    override fun intoString(value: Duration): String {
        return value.svgString
    }
}

internal val Duration.svgString: String get() = toString()

internal fun durationAttribute(name: String) = svgAttribute(name, DurationTypeToken)
