package dev.jamesyox.statik

import kotlinx.css.CssBuilder
import kotlinx.css.StyledElement
import org.w3c.dom.Element
import org.w3c.dom.HTMLElement

public fun Element.css(block: CssBuilder.() -> Unit) {
    setAttribute(
        qualifiedName = "style",
        value = CssBuilder().apply(block).toString()
    )
}

public fun Element.css(styledElement: StyledElement) {
    setAttribute(
        qualifiedName = "style",
        value = styledElement.declarations.toString()
    )
}