package dev.jamesyox.svgk.tags

import dev.jamesyox.svgk.SvgTag
import dev.jamesyox.svgk.TagConsumer
import dev.jamesyox.svgk.attr.AttributeContainer
import dev.jamesyox.svgk.consumeTag
import dev.jamesyox.svgk.consumeTerminalTag

public data object Text : SvgTag {
    override val tagName: String = "text"
}

context(tagConsumer: TagConsumer<T>)
public fun <T> text(
    content: context(AttributeContainer, Text) () -> Unit
) : T {
    return consumeTag(
        tag = Text,
        content = { context(Text) { content() } }
    )
}