package dev.jamesyox.svgk.attr.types

internal data object BooleanAttributeToken : AttributeTypeToken<Boolean> {
    override fun intoString(value: Boolean): String = value.svgString
}

internal val Boolean.svgString: String get() = if (this) "1" else "0"

internal fun booleanAttribute(name: String): SvgAttribute<Boolean> {
    return SvgAttribute(name, BooleanAttributeToken)
}
