package dev.jamesyox.svgk.attr

public class InMemoryAttributeContainer : AttributeContainer {
    private val _attributes: MutableMap<String, String> = mutableMapOf()
    public override val attributesMap: Map<String, String> get() = _attributes.toMap()
    // HTML

    override operator fun set(key: String, value: String) {
        _attributes[key] = value
    }

    override operator fun get(key: String): String? {
        return _attributes[key]
    }

    internal fun clear() {
        _attributes.clear()
    }
}

public inline fun svgAttributes(block: AttributeContainer.() -> Unit): InMemoryAttributeContainer {
    return InMemoryAttributeContainer().apply(block)
}