package dev.jamesyox.statik.css

import kotlinx.css.CssBuilder
import kotlinx.css.TagSelector

public sealed interface Selector {
    public val name: String
    public val selectorText: String

    public class Class(
        override val name: String
    ) : Selector {
        override val selectorText: String = ".$name"
    }

    public class Id(
        override val name: String
    ) : Selector {
        override val selectorText: String = "#$name"
    }

    public class Tag(
        override val name: String
    ) : Selector {
        override val selectorText: String = name
    }

    public companion object {
        public fun Tag(tagSelector: TagSelector): Tag {
            return Tag(tagSelector.tagName)
        }
    }
}


