package dev.jamesyox.svgk.attr.objs.pathd

public class PathD {
    private val commands: MutableList<PathCommand> = mutableListOf()

    internal val list: List<PathCommand> get() = commands.toList()

    public fun M(x: Number, y: Number) {
        commands.add(MoveTo(x,y))
    }

    public fun L(x: Number, y: Number) {
        commands.add(LineTo(x,y))
    }

    public fun H(x: Number) {
        commands.add(HorizontalLineTo(x))
    }

    public fun h(dx: Number) {
        commands.add(HorizontalLine(dx))
    }

    // TODO: An odd choice.... Hacky but looks kind of nice?
    public val Z: Unit get() {
        commands.add(ClosePath)
    }

    public fun Q(x1: Number, x2: Number, x: Number, y: Number) {
        commands.add(QuadraticBezierCurve(x1, x2, x, y))
    }

    public fun A(rx: Number, ry: Number, angle: Number, largeArcFlag: Boolean, sweepFlag: Boolean, x: Number, y: Number) {
        commands.add(EllipticalArcCurve(rx, ry, angle, largeArcFlag, sweepFlag, x, y))
    }

    public fun C(x1: Number, y1: Number, x2: Number, y2: Number, x: Number, y: Number) {
        commands.add(CubicCurve(x1, y1, x2, y2, x, y))
    }

    public fun c(dx1: Number, dy1: Number, dx2: Number, dy2: Number, dx: Number, dy: Number) {
        commands.add(CubicCurveRelative(dx1, dy1, dx2, dy2, dx, dy))
    }

    override fun toString(): String {
        return commands.joinToString(separator = " ") { it.svgString }
    }
}

public fun pathD(block: PathD.() -> Unit): String = PathD().apply(block).toString()

internal fun pathDList(block: PathD.() -> Unit): List<PathCommand> = PathD().apply(block).list

